} from '../services/config'
export default {
    name: 'file_upload',
    data() {
        return {
            valid: false,
            user: {},
            contexto: ["contabil", "fisica", "pessoal"],
            arquivo: {
                titulo: '',
                complemento: '',
                tipo: '',
                desc: '',
                contexto: ''
            },
            headersRes: [{
                    text: 'Titulo',
                    value: 'title'
                },
                {
                    text: 'Complemento',
                    value: 'complemento'
                },
                {
                    text: 'Data de publicao',
                    value: 'createDate'
                }
            ],
            headers: [{
                    text: 'Tipo',
                    align: 'left',
                    value: 'tipo'
                },
                {
                    text: 'Titulo',
                    value: 'title'
                },
                {
                    text: 'Complemento',
                    value: 'complemento'
                },
                {
                    text: 'Contexto',
                    value: 'contexto'
                },
                {
                    text: 'Data de publicao',
                    value: 'createDate'
                },
            ],
            uploadPercentage: 0,
            title: '',
            loader: null,
            loading: false,
            check: false,
            search: '',
            filers: [],
            mArquivos: [],
            color: '',
            file: {},
            rota: 'http://191.252.200.167:3000/file/send',
            url: '',
            tipoArquivos: [{
                    tipo: 'CADASTRO DE FUNCIONRIO',
                    desc: 'ANEXAR ARQUIVOS: COPIA DA CTPS, RG, CPF, COMPROVANTE DE RESIDNCIA, TITULO, CERTIDO DE CASAMENTO, CERTIDO DE NASCIMENTO DOS DEPENDENTES, EXAME ADMISSIONAL' + ' ' +
                        'RECEPO DE DOCUMENTOS: CONTRATO DE EXPERIENCIA, ETIQUETAS DA CTPS, TERMO DE RESPONSABILIDADE, RECIBO DE ENTREGA DA CTPS'
                },
                {
                    tipo: 'FOLHA',
                    desc: 'ANEXAR ARQUIVOS: FALTAS, HORAS EXTRAS, ADICIONAIS E DESCONTOS' + ' ' +
                        'RECEPO DE DOCUMENTOS: FOLHA DE PAGAMENTO, CONTRA-CHEQUE, GPS, GRF, TAXA SINDICAL, DARF,'
                },
                {
                    tipo: 'FRIAS',
                    desc: 'ANEXAR ARQUIVOS' + " " +
                        'RECEPO DE DOCUMENTOS: PREVISO DE FRIAS, AVISO DE FRIAS, RECIBO DE FRIAS, FRIAS COLETIVA'
                },
                {
                    tipo: 'RESCISO',
                    desc: 'ANEXAR ARQUIVOS' + ' ' +
                        'RECEPO DE DOCUMENTOS: TRCT, SIMULAO RESCISO, RESCISO COLETIVA, SALRIO CONTRIBUIO, AVISO PREVIO/DISPENSA'
                },
                {
                    tipo: 'AFASTAMENTO',
                    desc: 'ANEXAR ARQUIVOS:  LAUDO MDICO, ATESTADO MDICO RECEPO DE DOCUMENTOS'
                },
                {
                    tipo: 'DCIMO TERCEIRO',
                    desc: 'ANEXAR ARQUIVOS' + ' ' +
                        'RECEPO DE DOCUMENTOS: FOLHA PRIMEIRA PARCELA, FOLHA SEGUNDA PARCELA, GPS 13, CONTRA-CHEQUE PRIMEIRA PARCELA, CONTRA-CHEQUE SEGUNDA PARCELA'
                },
                {
                    tipo: 'NOTAS FISCAIS',
                    desc: 'ANEXAR ARQUIVOS: XML, ARQUIVO SPED,  INVENTRIO RECEPO DE DOCUMENTOS: FATURAMENTO, INVENTRIO'
                },
                {
                    tipo: 'ARQUIVOS MAGNTICOS',
                    desc: 'ANEXAR ARQUIVOS: SPED ICMS, EFD CONTRIBUIES RECEPO DE DOCUMENTOS'
                },
                {
                    tipo: 'ICMS e IPI',
                    desc: 'ANEXAR ARQUIVOS: RECEPO DE DOCUMENTOS: APURAO E SALDO DE ICMS, GUIAS ESTADUAIS, DARF IPI, LIVROS FISCAIS'
                },
                {
                    tipo: 'FEDERAL',
                    desc: 'ANEXAR ARQUIVOS: ARQUIVOS SPED CONTRIBUIES, SPED ICMS, SPED REINF, RESUMO DE TRIBUTOS, DIRF CARTO DE CRDITO RECEPO DE ARQUIVOS: DAS, DAS PARCELAMENTO SIMPLES NACIONAL, DARF PIS, DARF COFINS, DARF CSLL, DARF IRPJ, DARF RETENES, DEFIS/DASN'
                },
                {
                    tipo: 'MUNICIPAL',
                    desc: 'ANEXAR ARQUIVOS: NOTAS DE SERVIOS, RELATRIOS DE RETENO DE SERVIO' + ' ' +
                        'RECEPO DE DOCUMENTOS: LIVROS FISCAIS, ISS, ISS UNIPROFISSIONAL, GUIAS MUNICIPAIS'
                },
                {
                    tipo: 'ESCRITA CONTBIL',
                    desc: 'ANEXAR ARQUIVOS: TXT MOVIMENTAO BANCRIA, LIVRO CAIXA, CONTRATOS DE FINANCIAMENTO, CONTRATO DE EMPRESTIMO, CONTRATO ARRENDAMENTO, ITENS IMOBILIZADOS, ESCRITA DE IMOVEIS' + ' ' +
                        'RECEPO DE ARQUIVOS: LALUR, BALANO PATRIMONIAL, LIVRO CAIXA, ECF e FCONT, BALANCETE, RAZO, DIRIO, DEMONSTRAO DE RESULTADO DO EXERCICIO, ANLISE POR CENTRO DE CUSTO'
                }
            ]
        }
    },
    props: {
        id: {
            type: String
        }
    },

    components: {
        'upload-btn': UploadButton
    },
    methods: {
        fileChanged(file) {
            this.color = 'blue-grey',
                this.file = file;
            this.title = file.name;
            this.check = false;
            this.uploadPercentage = 0;
        },
        resetArquivo() {
            this.arquivo.titulo = ''
            this.arquivo.complemento = ''
        },
        async listarArquivos() {
            await services.getFiles(this.id).then(r => {
                if (r.status == 200) {
                    var arquivos = [];
                    var arquivo = {};
                    for (let index = 0; index < r.data.length; index++) {
                        arquivo = r.data[index];
                        arquivo.createDate = this.formatDate(r.data[index].createDate);
                        if (arquivo.tipo === 'CLIENTE') {
                            this.mArquivos.push(arquivo);
                        } else {
                            arquivos.push(arquivo);
                        }
                    }
                    this.filers = arquivos;
                }
            })
        },

        formatDate(date) {
            var mDate = new Date().toISOString().
            replace(/T/, ' '). // replace T with a space
            replace(/\..+/, '');
            if (!mDate) return null
            const [year, month, day] = mDate.split('-')
            const [mDay, horas] = day.split(' ')
            return `${mDay}/${month}/${year} - ${horas}`
        },
        teste(via) {
            this.loader = 'loading3';
            const l = this.loader
            this[l] = !this[l]
            this.arquivo.idUser = this.id;
            this.arquivo.via = via;
            let formData = new FormData();
            formData.append('arquivo', JSON.stringify(this.arquivo));
            formData.append('sampleFile', this.file);
            this.fileUpoad(formData).then(r => {
                this[l] = false;
                this.loader = null;
                this.uploadPercentage = 0;
                this.color = 'green';
                this.check = true;
                this.listarArquivos(this.id);
                setTimeout(() => (this.resetArquivo()), 1000)
            }).catch(function () {

            });
        },
        fileUpoad(data) {
            return http.post('file/send', data, {
                headers: {
                    'Content-Type': 'multipart/form-data'
                },
                onUploadProgress: function (progressEvent) {
                    this.uploadPercentage = parseInt(Math.round((progressEvent.loaded * 100) / progressEvent.total));
                }.bind(this)
            });
        },
        getDownalod(data) {
            var url = 'http://191.252.200.167:3000/file/download/' + data;
            //var url = 'http://localhost:3000/file/download/' + data;

            window.open(url)
        },
    },

    mounted() {
        this.user = this.user = JSON.parse(this.$cookie.get("user"));
        this.listarArquivos();
    },

    computed: {
        mUrl() {
            if (this.id != null) {
                return this.url = this.rota + '/' + this.title + '/' + this.id;
            }
        },
        // filteredList() {
        //     return this.filers.filter(post => {
        //         return post.title.toLowerCase().includes(this.search.toLowerCase())
        //     })
        // }
    }
}
</script>

